import moment from "moment";

const getDifferenceTime = (date, start) => {
  const time = moment(date).set({ "hour": moment(start).hour(), "minute": moment(start).minute() });
  const a = moment(time);
  const b = moment();
  if (a.diff(b) < 0) {
    return null;
  }
  return true;
};

const verifyNumber = value => {
  var numberRex = new RegExp("^[0-9]+$");
  if (numberRex.test(value)) {
    return true;
  }
  return false;
};

const verifyLetter = value => {
  var numberRex = new RegExp("^[a-zA-Z–∞-—è–ê-–Ø–Å—ë ]+$");
  if (numberRex.test(value)) {
    return true;
  }
  return false;
};

const verifyNumberPhone = (value, lengthStart, lengthEnd) => {
  if (verifyNumber(value) && value.length >= lengthStart && value.length < lengthEnd) {
    return true;
  }
  return false;
};

const verifyLength = (value, lth) => {
  if (value.trim().length >= lth) {
    return true;
  }
  return false;
};


const validate = values => {
  const errors = {};
  const { lastName, surname, phoneNumber, members } = values;
  if (!lastName) {
    errors.lastName = "–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ";
  }

  if (lastName && !verifyLength(lastName, 2)) {
    errors.lastName = "–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–µ –∏–º—è";
  }

  if (!verifyLetter(lastName)) {
    errors.lastName = "–ú–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤";
  }

  if (!surname) {
    errors.surname = "–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ";
  }

  if (surname && !verifyLength(surname, 3)) {
    errors.surname = "–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–µ –æ—Ç—á–µ—Å—Ç–≤–æ";
  }

  if (!verifyLetter(surname)) {
    errors.surname = "–ú–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤";
  }

  if (!phoneNumber) {
    errors.phoneNumber = "–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ";
  }

  if (phoneNumber && !verifyNumberPhone(phoneNumber, 6, 12)) {
    errors.phoneNumber = "–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –æ—Ç 6 –¥–æ 11 —Ü–∏—Ñ—Ä";
  }

  if (phoneNumber && !verifyNumber(phoneNumber)) {
    errors.phoneNumber = "–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ —Å–æ—Å—Ç–æ–∏—Ç —Ç–æ–ª—å–∫–æ –∏–∑ —Ü–∏—Ñ—Ä";
  }
  if (!members || !members.length) {
    errors.members = "–ù–µ—Ç –¥–∞–Ω–Ω—ã—Ö –æ –∑–∞–ø–∏—Å–∏";
  }

  if (members && members.length) {
    const membersArrayErrors = [];
    members.forEach((member, memberIndex) => {
      const memberErrors = {};
      if (!member || !member.title) {
        memberErrors.title = "–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –æ–ø–∏—Å–∞–Ω–∏–µ —É—Å–ª—É–≥–∏";
        membersArrayErrors[memberIndex] = memberErrors;
      }
      if (member.title && !verifyLength(member.title, 3)) {
        memberErrors.title = "–ü–æ–ª–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –Ω–µ –º–µ–Ω—å—à–µ 3 —Å–∏–º–≤–æ–ª–æ–≤";
        membersArrayErrors[memberIndex] = memberErrors;
      }
      if (!member || !member.date) {
        memberErrors.date = "–£–∫–∞–∂–∏—Ç–µ –¥–∞—Ç—É";
        membersArrayErrors[memberIndex] = memberErrors;
      }

      if (!member || !member.start) {
        memberErrors.start = "–£–∫–∞–∂–∏—Ç–µ –Ω–∞—á–∞–ª—å–Ω–æ–µ –≤—Ä–µ–º—è";
        membersArrayErrors[memberIndex] = memberErrors;
      }

      if (!member || !member.end) {
        memberErrors.end = "–£–∫–∞–∂–∏—Ç–µ –∫–æ–Ω–µ—á–Ω–æ–µ –≤—Ä–µ–º—è";
        membersArrayErrors[memberIndex] = memberErrors;
      }

      if (member && member.start && moment(member.start).hour() > 20) {
        memberErrors.start = "–ó–∞–ø–∏—Å—å –¥–æ 20:00";
        membersArrayErrors[memberIndex] = memberErrors;
      }
      if (member && member.start && member.end && !(moment(member.end).isAfter(member.start, "minute"))) {
        memberErrors.end = `–î–æ–ª–∂–Ω–æ –±—ã—Ç—å –±–æ–ª—å—à–µ ${moment(member.start).format("LT")}`;
        membersArrayErrors[memberIndex] = memberErrors;
      }
      if (member && member.end && (moment(member.end).hour() === 20 && moment(member.end).minute() > 0 || moment(member.end).hour() > 20)) {
        console.log(moment(member.end).hour() >= 20);
        memberErrors.end = "–ó–∞–ø–∏—Å—å –¥–æ 20:00";
        membersArrayErrors[memberIndex] = memberErrors;
      }
      if (member && member.start && (moment(member.start).hour() < 10 || moment(member.start).hour() >= 20)) {
        memberErrors.start = "–ó–∞–ø–∏—Å—å —Å 10:00 –¥–æ 19:55";
        membersArrayErrors[memberIndex] = memberErrors;
      }
      if (member && member.start && member.date && !getDifferenceTime(member.date, member.start)) {
        memberErrors.start = `–í—Ä–µ–º—è —É–∂–µ –ø—Ä–æ—à–ª–æ!`;
        membersArrayErrors[memberIndex] = memberErrors;
      }
      if (!member || !member.resourceId) {
        memberErrors.resourceId = "–í—ã–±–µ—Ä–∏—Ç–µ –∏–º—è –º–∞—Å—Ç–µ—Ä–∞/–°–æ—è–ª—è—Ä–∏–π";
        membersArrayErrors[memberIndex] = memberErrors;
      }
    });

    if (membersArrayErrors.length) {
      errors.members = membersArrayErrors;
    }
  }
  return errors;
};

export default validate;
