import "date-fns";
import React, { Fragment, Component } from "react";
import PropTypes from "prop-types";
import { TimePicker, DatePicker } from "material-ui-pickers";
import "moment/locale/ru";

class MaterialUIPickers extends Component {
  onChange = (...rest) => {
    this.props.input.onChange(rest);
  };

  componentDidMount() {
    if (this.props.valD) {
      this.props.input.onChange(this.props.valD);
    }
  }

  render() {
    const {
      isEnd,
      isTime,
      id,
      input,
      meta: { error, valid },
      onChange,
      onOpen,
      onClose,
      disabled
    } = this.props;

    return (
      <Fragment>
        {
          isTime
            ? <TimePicker
              autoOk
              id={id}
              ampm={false}
              label={isEnd ? "–ö–æ–Ω–µ—á–Ω–æ–µ –≤—Ä–µ–º—è" : "–ù–∞—á–∞–ª—å–Ω–æ–µ –≤—Ä–µ–º—è"}
              cancelLabel="–û—Ç–º–µ–Ω–∞"
              invalidLabel='–ù–µ —É–∫–∞–∑–∞–Ω–æ'
              minutesStep={5}
              margin="normal"
              minDate={new Date()}
              disabled={disabled}
              helperText={error}
              error={(!valid && error) !== false}
              invalidDateMessage={`–£–∫–∞–∂–∏—Ç–µ ${isEnd ? "–∫–æ–Ω–µ—á–Ω–æ–µ –≤—Ä–µ–º—è" : "–Ω–∞—á–∞–ª—å–Ω–æ–µ –≤—Ä–µ–º—è"}`}
              {...input}
            />
            : <DatePicker
              autoOk
              showTodayButton
              id={id}
              label="–í—ã–±–µ—Ä–∏—Ç–µ –¥–∞—Ç—É"
              format="DD/MM/YYYY"
              todayLabel="–°–µ–≥–æ–¥–Ω—è"
              cancelLabel="–û—Ç–º–µ–Ω–∞"
              invalidLabel="–ù–µ —É–∫–∞–∑–∞–Ω–∞"
              invalidDateMessage="–í—ã–±–µ—Ä–∏—Ç–µ –¥–∞—Ç—É"
              variant="outlined"
              margin="normal"
              disabled={disabled}
              minDate={new Date()}
              onChange={onChange}
              onOpen={onOpen}
              onClose={onClose}
              {...input}
            />
        }
      </Fragment>
    );
  }
}

MaterialUIPickers.propTypes = {
  isEnd: PropTypes.bool,
  isTime: PropTypes.bool,
  error: PropTypes.string,
  valid: PropTypes.bool,
  disabled: PropTypes.bool,
  valD: PropTypes.object,
  id: PropTypes.string,
  input: PropTypes.object,
  onChange: PropTypes.func,
  onOpen: PropTypes.func,
  onClose: PropTypes.func
};

export default MaterialUIPickers;
