import React, { Component, Fragment } from "react";
import PropTypes from "prop-types";

import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";

import GridContainer from "components/Grid/GridContainer.jsx";
import ActonTill from "../../views/Till/ActonTill";
import isOnline from "is-online";
// core components
import Admin from "layouts/Admin.jsx";
import Error from "components/Modal/Error";

import "assets/css/material-dashboard-react.css?v=1.6.0";
import { startApp } from "../../modules/Admin";
import Progress from "components/Progress/Progress";

class App extends Component {

  state = {
    isClick: false
  };

  componentDidMount() {
    this.props.startApp();
  }

  isValidData = data => {
    if (!data || typeof data !== "object") return false;
    const keys = Object.keys(data);
    return !!keys.length;
  };

  handleClickAddInTill = data => {
    if (!this.isValidData(data)) return;
    return this.props.loadTill(data);
  };

  changeClick = () => {
    this.setState({
      isClick: true
    });
  };

  render() {
    const { administrators, isDay, isError } = this.props;
    const { isClick } = this.state;
    // console.log(isError);
    if (isError === null) {
      return (
        <Progress/>
      );
    }
    else if (isError === true) {
      return <Error/>;
    }
    return (
      <Fragment>
        {/*{!this.state.isClick && !isDay*/}
        {/*{!isClick ?*/}
        {/*<div*/}
        {/*className="loader-view-block">*/}
        {/*<div className="loader-view">*/}
        {/*<CircularProgress/>*/}
        {/*</div>*/}
        {/*</div>*/}
        {/*: null*/}
        {/*}*/}
        {!isDay && !isClick
          ? <Dialog
            maxWidth={"lg"}
            // fullScreen={lock}
            fullScreen={true}
            fullWidth={true}
            open={!isDay}
            // open={!!(lock && isLoad) && !viewTill}
            // onClose={inTill.length ? this.handleClickLock : () => ({})}
            scroll="body"
          >
            <DialogTitle>
              –î–ª—è –ø—Ä–æ–¥–æ–ª–∂–µ–Ω–∏—è –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –≤—ã–±—Ä–∞—Ç—å –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞, –≤–Ω–µ—Å—Ç–∏ —Å—É–º–º—É –ø—Ä–∏—Ö–æ–¥–∞ –∏ –æ—Ç–∫—Ä—ã—Ç—å —Å–º–µ–Ω—É.
            </DialogTitle>
            <DialogContent>
              <GridContainer
                direction={"row"}
                justify={"center"}
                alignItems={"center"}
                alignContent={"center"}
              >
                <ActonTill
                  handleClickAdd={this.handleClickAddInTill}
                  btnAdd={"–û—Ç–∫—Ä—ã—Ç—å —Å–º–µ–Ω—É"}
                  label={"–í–≤–µ–¥–∏—Ç–µ —Å—É–º–º—É"}
                  changeClick={this.changeClick}
                  options={administrators}
                  selectName={"administrators"}
                  selectLabel={"–í—ã–±–µ—Ä–∏—Ç–µ –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞"}
                  isOutTill
                  // —Ä–µ—à–∏—Ç—å –≤–æ–ø—Ä–æ—Å –∫–∞—Å–∞—Ç–µ–ª—å–Ω–æ –≤–Ω–µ—Å–µ–Ω–∏—è —Å—É–º–º—ã —á—Ç–æ–±—ã –º–æ–≥ —Ç–æ–ª—å–∫–æ –æ–¥–∏–Ω —Ä–∞–∑ –≤–Ω–µ—Å—Ç–∏ —Å—É–º–º—É
                />
              </GridContainer>
            </DialogContent>
            <DialogActions>
            </DialogActions>
          </Dialog>
          : isClick && !isDay ? <Progress/> : <Admin {...this.props}/>
        }
      </Fragment>
    );
  }
}

App.defaultProps = {};

App.propTypes = {
  classes: PropTypes.object
};

export default App;
